




















































































import Vue from "vue";
import Component from "vue-class-component";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast } from "vant";
import comhead from "../../components/Comhead.vue";

@Component({
  components: {
    comhead
  },
  mixins: [mixin]
})
export default class collar_center extends Vue {
  handlePX(CssName, PxNumber) {
    return (
      CssName +
      ":" +
      this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availWidth /
        750 *
        PxNumber +
      "px;"
    );
  }
  loading=false
  pageSize = 20;
  couponList=[];
  page=0;
  loadMore(){
    if(!this.goodsId){

    let self = this;
    self.loading=true;    
    setTimeout(() => {
      if(!self.loading){
        self.page+=1;
        self.getList();
        self.loading = false;
      }
    }, 1000);
    }


  }

  getList() {
    console.log('this.goodsId ',this.goodsId)

if(this.goodsId){


Vue.prototype.$reqFormPost(
      "/coupon/goods/list",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        goodsId: this.goodsId,
      },
      res => {
      if (res == null) {
        console.log("网络请求错误！");
        return;
      }
      if (res.data.status != 200) {
        console.log(
          "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
        );
        Toast(res.data.message);
        return;
      }
        this.couponList = res.data.data
      }
    );

}else{

 Vue.prototype.$reqFormPost("/coupon/center/list", {
      userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
        .userId,
      token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
        .token,
      page: this.page,
      pageSize: 20
 }, res => {
      if (res == null) {
        console.log("网络请求错误！");
        return;
      }
      if (res.data.status != 200) {
        console.log(
          "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
        );
        Toast(res.data.message);
        this.$router.push({name:'login'})
        return;
      }
  console.log(res)
    this.couponList = res.data.data.couponList;
    console.log("领卷中心",res.data.data.couponList);
       if (res.data.data.couponList.length == 20) {
          this.loading = false;
        }
    });
}
  }

  addcoupon(couponId){
    Vue.prototype.$reqFormPost("/coupon/user/linkadd", {
      userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
        .userId,
      token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
        .token,
        couponId:couponId
    }, res => {
      if (res == null) {
        console.log("网络请求错误！");
        return;
      }
      if (res.data.status != 200) {
        console.log(
          "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
        );
        Toast(res.data.message);
        return;
      }
        Toast("领券成功");
      this.getList();
    });
  }
goodsId = null;
  mounted() {
this.goodsId = this.$route.query.goodsId

    this.getList();
    console.log("领卷中心");
  }
}



